<!-- 【通知信息】管理页面 -->
<template>
  <div>
    <!--headers个人中心-->
    <personal></personal>
    <!--主页展示-->
    <service></service>
  </div>
</template>
<style scoped>
</style>
<script>
  import personal from '../tabBar/personal'
  import service from '../tabBar/service'
  import {mapActions} from "vuex";
  export default {
    components: {personal,service},
    mounted() {
      //加载配置数据
      this.commitDictList()
      this.commitConfig()
    },
    methods:{
      ...mapActions(['commitConfig', 'commitDictList']),
    }
  }

</script>




