<!-- 【通知信息】管理页面 -->
<template>
  <div class="client">
    <user-login ref="userLoginDialog"></user-login>
    <user-register ref="userRegisterDialog"></user-register>
    <user-info v-if="hasLogin" ref="userInfoDialog"/>
    <user-password v-if="hasLogin" ref="passwordDialog"/>
    <div class="banner banner-background">
      <div class="banner-head banner-head-background">
        <sui-clock class="clock" style="float: left;padding: 5px"></sui-clock>
        <div class="banner-btn">
          <div v-if="hasLogin" style="padding: 0 30px" >
            <el-dropdown >
              <div style="padding-right: 8px">
              <el-image v-if="hasLogin" class="head-img" :src="formatUrl(userInfo.photo)"></el-image>
              <span class="banner-font">{{userInfo.loginName}}</span>
              <i class="el-icon-arrow-down banner-font"></i>
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="updateUserInfo({title:'修改信息',pageFrom:'edit',id:userInfo.id})">修改信息</el-dropdown-item>
                <el-dropdown-item @click.native="updatePassword({title:'修改密码',pageFrom:'edit',id:userInfo.id})">修改密码</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-button size="mini" type="text" class="banner-font"  @click="loginOut">退出登录</el-button>
          </div>
          <div style="padding: 0 50px" v-else>
            <el-button size="mini" type="text" class="banner-font"  @click="userLogin">登录</el-button>
            <el-button size="mini" type="text" style="text-decoration:underline;" class="banner-font"  @click="userRegister">注册</el-button>
          </div>
        </div>
      </div>
        <!--项目名称-->
<!--        <el-image  style="position: absolute;top: 0;right: 500px;height: 200px" src="/static/images/client/welcome.svg"/>-->
        <iframe style="float: right;margin: 20px" width="420" scrolling="no" height="60" frameborder="0" allowtransparency="true" src="http://i.tianqi.com/index.php?c=code&amp;id=12&amp;icon=1&amp;num=5"></iframe>
        <div class="project-name">
          <el-image class="logo" src="/static/images/client/logo.png"></el-image>
          {{projectName}}
        </div>
    </div>
  </div>
</template>
<script>
  import userLogin from '../tabBar/personal/userLogin.vue'
  import userRegister from '../tabBar/personal/userRegister.vue'
  import userPassword from '../tabBar/personal/userPassword.vue'
  import userInfo from '../tabBar/personal/userInfo.vue'
  import suiClock from '@/components/sui-clock/sui-clock.vue'
  import {mapActions} from "vuex";
  export default {
    components: {userLogin,userRegister,userPassword,userInfo,suiClock},
    computed:{
      userInfo(){
        return this.$store.getters.userInfo;
      },
      hasLogin(){
        return this.$store.getters.hasLogin;
      },
      projectName(){
        return this.$store.getters.config.projectName;
      }
    },
    methods: {
      ...mapActions(['commitClientLoginInfo']),
      loginOut(){
        const that = this;
        that.showConfirm('确认退出吗?',function (){
          that.$request.doGet("/api/client/login/loginOut").then(res=>{
            if(res.isOk){
              that.commitClientLoginInfo();//更新登录信息
              that.showToast("退出成功");
            }
          })
        })
      },
      userLogin() {
        this.$refs.userLoginDialog.open({title:'用户登录'});
      },
      userRegister() {
        this.$refs.userRegisterDialog.open({title:'用户注册'});
      },
      updateUserInfo(option) {
        this.$refs.userInfoDialog.open(option);
      },
      updatePassword(option) {
        this.$refs.passwordDialog.open(option);
      },
    }
  }
</script>
<style scoped>
  .banner {
    height: 150px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  .banner-font{
    color: white;
  }
  .banner-font:hover{
    color: #e8f4ff;
  }
  .head-img {
    vertical-align:middle;
    margin-right: 5px;
    width: 20px;
    height: 20px;
    border-radius: 50%
  }
  .logo {
    background: white;
    vertical-align:middle;
    margin-right: 5px;
    width: 50px;
    height: 50px;
    border-radius: 50%
  }
  .banner-head{
    height: 35px;
  }
  .banner-btn{
    margin-top: 3px;
    float: right;
  }
  .clock{
    margin: 3px 5px;
    color: white;
    font-size: small
  }
  .project-name{
    margin-top: 30px;
    margin-left: 100px;
    font-size: 30px;
    text-shadow: 1px 3px 3px #000000;
    color: #e8f4ff;
    font-weight: bold;
  }
</style>




