<!-- 【用户信息】弹窗页面 -->
<template>
  <div style="font-family:cursive;height: 100vh;padding:50px 300px">
    <el-row style="height: 600px">
      <el-breadcrumb style="position: relative;top: 0;left: 12.5%;margin: 5px 5px;" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item><span style="font-weight: bold;color: rgba(43,74,120,0.88);font-size: medium">用户中心</span></el-breadcrumb-item>
        <el-breadcrumb-item><span style="font-weight: bold;color: #2b4a78;font-size: medium">{{targetName}}</span></el-breadcrumb-item>
      </el-breadcrumb>
      <el-col :span="3" class="div-shadow" style="border: solid 1px #889db6;height: 100%;background: #FFFBF2;border-radius: 5px;padding-top: 10px;">
        <div :class="['menu-span div-shadow font-shadow',{'isActive': active==1}]" @click="onclick($event,1)">用户信息</div>
        <div :class="['menu-span div-shadow font-shadow',{'isActive': active==2}]" @click="onclick($event,2)">修改密码</div>
        <div :class="['menu-span div-shadow font-shadow',{'isActive': active==3}]" @click="onclick($event,3)">消息列表</div>
        <div :class="['menu-span div-shadow font-shadow',{'isActive': active==4}]" @click="onclick($event,4)">发布通知</div>
      </el-col>
      <el-col :span="21" class="div-shadow" style="border:solid 1px rgba(136,157,182,0.48);padding:50px;height: 100%;background: #FFFBF2;border-radius: 5px">
        <div v-if="hasLogin">
          <user-info v-if="active==1"></user-info>
          <user-password v-if="active==2"></user-password>
          <notice-list v-if="active==3"></notice-list>
          <publish-notice v-if="active==4"></publish-notice>
        </div>
        <el-empty v-else description="请先进行登录"><el-button size="mini" @click="openDialog({title:'用户登录'})">登录</el-button></el-empty>
        <user-login ref="userLoginDialog"></user-login>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  import userLogin from "../personal/userLogin";
  import userInfo from "../service/userService/userInfo";
  import publishNotice from "../service/userService/publishNotice";
  import noticeList from "../service/userService/noticeList";
  import userPassword from "../service/userService/userPassword";
  export default {
    components: {userLogin,userInfo,userPassword,suiPhoto,noticeList,publishNotice},
    name: "sysUserForm",
    data() {
      return {
        //弹窗设置
        targetName:"用户信息",
        active:1,
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单校验
        rules: {
        }
      }
    },
    mounted() {
    },
    computed:{
      hasLogin(){
        return this.$store.getters.hasLogin;
      }
    },
    methods: {
      //打开弹窗
      openDialog(option){
        this.$refs.userLoginDialog.open(option);
      },
      onclick(enent,index){
        this.active=index;
        this.targetName = enent.currentTarget.innerText;
      }
    }
  }
</script>

<style scoped>
  .menu-span {
    color: #333333;
    font-weight: bold;
    padding: 5px;
    margin: 10px;
    border-radius: 5px;
    text-align: center;
    background: rgba(245, 222, 179, 0.49);
  }
  .isActive{
    color: #2b4a78;
    background:wheat ;
  }
  .font-shadow{
    text-shadow: 1px 1px 3px #ffffff;
  }
  .menu-span:hover {
    color: #8c939d;
    background:wheat ;
  }
</style>



