<!-- 【用户信息】弹窗页面 -->
<template>
  <el-dialog  width="500px" center :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="登录名" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="请输入登录名" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="email">
        <el-input v-model="sysUser.password" type="password" placeholder="请输入密码" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="login">登 录</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  import {mapActions} from "vuex";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        roleList: [],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysUser: {
          loginName: "",
          password: ""
        },
        //表单校验
        rules: {
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['commitClientLoginInfo']),
      initData() {
        const loading = this.$loading({
          lock: true,
          text: '请稍后,登录中...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
      },
      login() {
        const loading = this.$loading({
          lock: true,
          text: '请稍后,登录中...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
        this.$request.doPost("/api/client/login/userLogin", this.sysUser).then(res => {
          if (res.isOk) {
              //获取登录信息
              this.commitClientLoginInfo().then(res => {
                loading.close();
                this.dialog.open = false;
                this.showSuccess("登录成功");
              })
          } else {
            this.showWarn(res.info);
          }
        });
      },
      //打开弹窗
      open(option) {
        this.showDialog(option.title, false);
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



