<!-- 【通知信息】弹窗页面 -->
<template>
  <div>
    <user-login ref="userLoginDialog"></user-login>
    <el-tabs class="client"  v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="网站首页" name="homeService">
        <homeService ref="homeService" ></homeService>
      </el-tab-pane>
      <el-tab-pane label="通知公告" name="noticeService">
        <noticeService ref="noticeService"></noticeService>
      </el-tab-pane>
      <el-tab-pane label="个人中心" name="userService">
        <userService ref="userService"></userService>
      </el-tab-pane>
      <el-tab-pane label="留言版" name="messageService">
        <messageService ref="messageService"></messageService>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import userLogin from './personal/userLogin'
  import homeService from './service/homeService'
  import noticeService from './service/noticeService'
  import userService from './service/userService'
  import messageService from './service/messageService'
  export default {
    components: {userLogin,homeService,noticeService,userService,messageService},
    data() {
      return {
        activeName: 'homeService'
      };
    },
    methods: {
      handleClick(tab, event) {
        if (this.activeName == 'homeService') {
          this.$refs.homeService.getList()
        } else if (this.activeName == 'noticeService') {
          this.$refs.noticeService.getList()
        } else if (this.activeName == 'userService') {
          //this.$refs.userService.getList()
        } else if (this.activeName == 'messageService') {
          this.$refs.messageService.getList()
        }
      },
    }
  };
</script>
<style scoped>
  .client{
    background:#EFEFEF;
  }
</style>





