<!-- 【用户信息】弹窗页面 -->
<template>
  <div>
    <el-form :model="user" ref="passwordForm" :rules="rules" label-width="100px">
      <el-form-item label="登录名" prop="loginName">
        <el-input v-model="user.loginName" disabled placeholder="请输入登录名" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="原始密码" prop="oldPassword">
        <el-input v-model="user.oldPassword" type="oldPassword" placeholder="请输入原始密码" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="newPassword">
        <el-input v-model="user.newPassword" type="newPassword" placeholder="请输入新密码" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="prePassword">
        <el-input v-model="user.prePassword" type="prePassword" placeholder="请输入确认密码" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('passwordForm')">确认修改</el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
  export default {
    name: "passwordForm",
    data() {
      return {
        roleList: [],
        checkRoleList: ["普通用户"],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        user: {
          loginName:this.$store.getters.userInfo.loginName,
          oldPassword: "",
          newPassword: "",
          prePassword: ""
        },
        //表单校验
        rules: {
          oldPassword: [{required: true, message: '请输入旧密码', trigger: 'blur'}],
          newPassword: [{required: true, message: '请输入新密码', trigger: 'blur'}],
          prePassword: [{required: true, message: '请输入确认密码', trigger: 'blur'}]
        }
      }
    },
    mounted() {
    },
    methods: {
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            if(this.user.newPassword!=this.user.prePassword){
              this.showWarn("两次密码不一致");
              return
            }
            that.$request.doPost('/api/client/sysUser/modifyPsd', that.user).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess(res.info);
                that.$emit('reloadList');
              }else{
                this.showWarn(res.info)
              }
            });
          }
        });
      },
    }
  }
</script>

<style scoped>
</style>



