<!-- 【用户信息】弹窗页面 -->
<template>
  <div style="height: 100vh;overflow-y: auto;padding:0 100px;">
    <notice-form ref="noticeDialog"></notice-form>
    <div v-if="dataList.length>0">
      <el-row style="background: #efefef;">
        <el-col style="padding: 10px" v-for="(notice, index) in dataList" :key="index" :span="6">
            <el-card  :body-style="{ padding: '10px'}">
              <img :src="formatUrl(notice.picture)" class="image">
              <div style="padding: 14px;">
                <span>{{notice.title}}</span>
                <div class="bottom">
                  <time class="time">{{notice.publishTime}}</time>
                  <div style="float: right">
                    <el-button size="mini" type="normal" @click="openDialog({title:'查看通知',pageFrom:'view',id:notice.id})">查看详情</el-button>
                    <el-button size="mini" type="primary" @click="openDialog({title:'编辑通知',pageFrom:'edit',id:notice.id})">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(notice.id)">删除</el-button>
                  </div>
                </div>
              </div>
            </el-card>
        </el-col>
      </el-row>
      <el-pagination
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-size="queryForm.size"
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <el-empty v-else description="暂无数据"></el-empty>
  </div>

</template>

<script>
  import noticeForm from '@/pages/module/notice/noticeForm'
  export default {
    components: {noticeForm},
    name: "noticeService",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        dataList:[],
        total: 0, //记录数
        //表单数据
        queryForm: {
          current: 1,
          size: 8,
          picture: "",
          title: "",
          type: "",
          publishTime: "",
          content: ""
        },
        //表单校验
        rules: {
        }
      }
    },
    mounted() {
      this.getList()
    },
    methods: {
      //打开弹窗
      openDialog(option) {
        this.$refs.noticeDialog.open(option);
      },
      //选中行删除
      handleDelete(id) {
        const that = this;
        that.showConfirm('确定删除选中的除数据吗?', function () {
          that.delete(id);
          that.showSuccess("删除成功");
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/client/login/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      getList() {
        this.$request.doPost('/api/client/notice/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //选择页码
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //删除方法
      delete(id) {
        this.$request.doGet('/api/client/notice/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .image {
    height: 200px;
    width: 100%;
    display: block;
  }
</style>



