<!-- 【通知信息】弹窗页面 -->
<template>
  <div style="height: 100vh;overflow-y: auto;padding:0 300px;background: #EFEFEF;">
    <!--    轮播区域-->
    <el-row style="height: 300px;border: solid 1px #ffffff;border-radius: 10px">
      <el-col :span="12">
        <el-carousel :interval="5000">
          <el-carousel-item v-for="item in wheelList" :key="item">
            <el-image :src="formatUrl(item.picture)" style="width: 100%;height: 100%;border-radius: 10px;"/>
            <div style="height: 50px;background: rgba(76,135,188,0.29);position: relative;top: -30px;padding: 0 10px;font-weight: bold;color: white">{{item.content}}</div>
          </el-carousel-item>
        </el-carousel>
      </el-col>
      <el-col :span="12" style="text-shadow: 1px 1px 3px #1f2d3d;">
        <div style="margin: 10px;line-height: 30px">
          <p>你现在还有时间,尽你自己最大的努力,努力做成你最想做的那件当,成为你最想成为的那种人,过你最想过的种生活,这个世界水远比你相的要距桔形,不要败给生活</p>
          <p>单枪匹马你怕,一控孤西又如何,这一路你可以突,但你一定不能。不拼一把,怎么知道自己起人物还是废物!</p>
          <p>你是你的敌人,只有你才能打倒你:你是你的上帝。只有你才能你!</p>
        </div>
      </el-col>
    </el-row>

    <el-row style="margin-top:30px;text-align: center;clear: both">
      <el-input v-model="queryForm.title" placeholder="请输入查询信息" clearable style="width: 300px;"></el-input>
      <el-button icon="el-icon-search"  circle @click="getList()"></el-button>
    </el-row>
    <!--弹窗详情-->
    <notice-form ref="noticeDialog"></notice-form>
    <div v-if="dataList.length>0" style="height:900px;">
      <el-row>
        <el-col style="padding: 10px" v-for="(notice, index) in dataList" :key="index" :span="6">
          <el-card  :body-style="{ padding: '10px'}">
            <img :src="formatUrl(notice.picture)"  class="image">
            <div style="padding: 14px;">
              <span>{{notice.title}}</span>
              <div class="bottom">
                <time class="time">{{notice.publishTime}}</time>
                <el-button type="text" class="button" @click="handleView(notice.id)">查看详情</el-button>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-pagination
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-size="queryForm.size"
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <el-empty v-else description="暂无数据"></el-empty>
  </div>
</template>

<script>
  import noticeForm from '@/pages/module/notice/noticeForm'
  export default {
    components: {noticeForm},
    data() {
      return {
        total: 0, //记录数
        queryForm: {
          current: 1,
          size: 8,
          picture: "",
          title: "",
          type: "",
          publishTime: "",
          content: ""
        },
        dataList:[]

      };
    },
    mounted() {
      this.getList()
    },
    computed:{
      wheelList(){
        return this.dataList.clone().splice(0,5);
      }
    },
    methods: {
      //点击查看
      handleView(id) {
        this.$refs.noticeDialog.open({title:'消息通知',pageFrom:'view',id:id});
      },
      //选择页码
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      getList() {
        this.$request.doPost('/api/client/notice/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
    }
  };
</script>
<style scoped>
  .project-name{
    float: left;
    font-size: 30px;
    text-shadow: 1px 3px 1px #ffc3a0;
    color: white;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-top: 30px;
    /*margin: 0 auto; !*水平居中*!*/
  }

  .el-card{
    width: 300px;
  }

  .el-tabs--top .el-tabs__item.is-top:nth-child(2) {
    padding-left: 20px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    height: 200px;
    width: 100%;
    display: block;
  }

</style>





