<!-- 【通知信息】管理页面 -->
<template>
    <div class="app-container" style="height: 500px;overflow-y: auto;border: solid 1px rgba(128,128,128,0.56);border-radius: 10px;background: white;">
      <el-collapse style="padding: 10px;" v-for="(notice,index) in dataList" :key="notice.id" v-model="activeNames" @change="handleChange">
        <el-collapse-item style="font-weight: bold" :title="(index+1+'、')+notice.title" name="1">
          <div><el-image :src="formatUrl(notice.picture)" style="height: 200px;width: 300px;border-radius: 10px"></el-image></div>
          <div>{{notice.content}}</div>
        </el-collapse-item>
      </el-collapse>
        <!-- 表格区域 -->
<!--        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">-->
<!--            <el-table-column label="id"  prop="id" v-if="false"/>-->
<!--            <el-table-column label="序号" type="index" width="50"/>-->
<!--            <el-table-column label="标题图片" prop="picture">-->
<!--                <template slot-scope="scope">-->
<!--                    <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>-->
<!--                </template>-->
<!--            </el-table-column>-->
<!--            <el-table-column label="消息标题" prop="title"/>-->
<!--            <el-table-column label="消息类型" prop="type">-->
<!--                <template slot-scope="scope">-->
<!--                    <span v-for="dict in typeDict" v-if="dict.dictValue==scope.row.type">{{dict.dictLabel}}</span>-->
<!--                </template>-->
<!--            </el-table-column>-->
<!--            <el-table-column label="消息内容" prop="content" show-overflow-tooltip/>-->
<!--        </el-table>-->
        <!-- 分页区域 -->
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :background="true"
                :current-page="queryForm.current"
                :page-sizes="[5,10,20,30,50]"
                :page-size="queryForm.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        loading: true,//是否加载完成
        total: 0, //记录数
        dataList: [],
        queryForm: {
          current: 1,
          size: 5,
          picture: "",
          title: "",
          type: "",
          publishTime: "",
          content: ""
        }
      };
    },
    mounted() {
      //初始化数据
      this.getList();
    },
    computed: {
      //【消息类型】
      typeDict() {
        return this.$store.getters.getDictArray('notice_type');
      },
    },
    methods: {
      //查询列表数据
      getList() {
        this.$request.doPost('/api/client/notice/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //选择页码
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
    }
  };
</script>
<style scoped>
</style>



