<!-- 【用户信息】弹窗页面 -->
<template>
  <div style="height: 100vh;overflow-y: auto;padding:0 400px;">
    <div v-if="$store.getters.hasLogin" class="font-shadow" style="padding: 5px"><span style="color: #1c84c6;font-weight: bold">{{$store.getters.userInfo.loginName}},</span><span style="color: #1c84c6;font-family: cursive">欢迎您到此一游!</span></div>
    <div v-else class="font-shadow" style="padding: 5px"><span style="color: #1c84c6;font-family: cursive">您还未登录,请先进行登录。</span></div>
    <div ref="editContent" class="div-shadow" @mouseleave="change" v-html="content" contentEditable='true' style="background:#5597bd;border:solid 1px gray;height: 200px;padding: 10px;color: white;border-radius: 5px;"></div>
    <el-button type="primary" size="mini" style="float: right;margin: 10px" @click="sendMsg">发表</el-button>
    <sui-face ref="face" @selectFace="selectFace"  style="padding: 10px;"></sui-face>
    <div v-if="dataList.length>0" style="clear: both;height:1800px;">
        <div style="padding-left: 20px;font-family: cursive" class="font-shadow">留言列表({{dataList.length}}条)</div>
        <div v-for="chat in dataList" class="chat-card">
            <el-image class="meg-head" :src="formatUrl(chat.photo)"></el-image>
            <div class="msg-content">
              <span style="color: #2b4a78;font-weight: bold">{{chat.loginName}}</span>
              <div class="msg-time">{{chat.createDate}}</div>
              <span style="color: #333;font-family: cursive" class="font-break" v-html="getHtml(chat.content)"></span>
              <div style="clear: both">
                <div style="font-size: small;color: #1c84c6;float: right;font-weight: bold;font-family: cursive">
                  <span class="msg-btn" style="margin-right: 10px" @click="reply(chat)">回复</span>
                  <span class="msg-btn" style="margin-right: 10px" @click="collect(chat)">点赞(5)</span>
                  <span class="msg-btn" style="margin-right: 10px;color: #f52514" @click="del(chat)">删除</span>
                </div>
                <div class="replyList" style="border-bottom: solid 1px white;clear: both;padding-top: 25px" v-if="chat.children" v-for="reply in chat.children">
                  <el-image style="display:block;margin-top:10px;height: 50px;width: 50px;border-radius:5px;float: left;border-radius: 50%;" :src="formatUrl(reply.photo)"></el-image>
                  <div style="float: left;padding: 5px;width: 700px;padding: 10px;font-size: small;">
                    <span style="color: rgba(43,74,120,0.8);font-weight: bold">{{reply.loginName}}</span>
                    <div style="color: #889db6;font-size:smaller">{{reply.createDate}}</div>
                    <span style="color: rgba(51,51,51,0.82);font-family: cursive" class="font-break">{{reply.content}}</span>
                  </div>
                </div>
              </div>
            </div>
        </div>
      <div style="clear: both"></div>
    </div>
    <el-empty class="position-center" description="暂无留言" v-else></el-empty>
  </div>
</template>

<script>
  import suiFace from "@/components/sui-face/sui-face.vue";
  export default {
    components:{suiFace},
    name: "serviceFifth",
    data() {
      return {
        show:false,
        content:"",
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        dataList:[],
        //表单数据
        message: {
          pid: "",
          userId: "",
          msgType: "",
          content: ""
        },
        //表单校验
        rules: {
        }
      }
    },
    mounted() {
      this.getList();
    },
    methods: {
      reply(chat) {
        const that =this;
        that.$prompt('请输入回复内容', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          if(value==null){
            this.showWarn("请输入内容!");
            return
          }
          that.$request.doPost('/api/client/message/sub',{pid:chat.id,msgType:chat.msgType, content: value}).then(res => {
            if (res.isOk) {
              that.showSuccess("回复成功");
              that.getList();
            }else{
              that.showWarn(res.info);
            }
          });
        })
      },
      collect(chat) {

      },
      del(chat) {
        const that = this;
        that.showConfirm("确认删除吗?", function () {
           that.$request.doPost('/api/client/message/delete', chat).then(res => {
              if (res.isOk) {
                that.showSuccess("删除成功");
                that.getList();
             };
          });
        })
      },
      getHtml(content) {
        return unescape(content);
      },
       //查询列表数据
       getList() {
       this.$request.doPost('/api/client/message/getList', this.message).then(res => {
          if (res.isOk) {
            this.dataList = res.list;
          }
        })
       },
      selectFace(face){
        this.content= this.content+face
        this.$refs.face.onclick()
      },
      change(){
        this.content = this.$refs.editContent.innerHTML;
      },
      sendMsg(){
        if(!this.$store.getters.hasLogin){
          this.showWarn("请先进行登录!");
          return
        }
        if(this.content==''){
          this.showWarn("请输入内容!");
          return
        }
        if(this.content.length>500){
          this.showWarn("输入内容过长!");
          return
        }

        const that =this;
        that.$request.doPost('/api/client/message/sub',{content:escape(this.content)}).then(res => {
          if (res.isOk) {
            that.showSuccess("提交成功");
            that.content="";
            that.getList();
          }else{
            that.showWarn(res.info);
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
  .chat-card {
    clear: both;
    padding: 10px;
  }
  .meg-head {
    padding: 5px;
    height: 100px;
    width: 100px;
    border-radius: 20px;
    float: left
  }

  .msg-time {
    color: #889db6;
    font-size: small
  }

  .msg-content {
    border-top: solid 1px white;
    float: left;
    padding: 5px;
    width: 800px;
    padding: 10px
  }
  .msg-btn:hover{
    color: rgba(136, 157, 182, 0.97);
    padding: 1px;
  }

</style>



